import React from 'react'

class CardLogin extends React.Component {

    render() {

        return(

            <div className="card md-3">

                <h3 className="card-header">{this.props.title}</h3>

                    <div className="card-body-login">
                            {this.props.children}
                    </div>               

            </div>

        )

    }

}

export default CardLogin